<template>
    <div class="login-layer" v-show="isShowLogin">
        <header class="login-mainheader-bar clearfix">
            <a class="login-back-btn" onclick="window.history.go(-1)"></a>
            <h2 class="login-title">登录磁易购社区</h2>
        </header>
        <main class="login-main">
            <section class="login-se-logo">
                <img src="../../../assets/images/ec_logo@3x.png" alt="磁易购" class="login-se-img" />
            </section>
            <section class="login-box">
                <div class="user-name-box user-name-box2">
                    <i class="user-icon"></i>
                    <input type="tel" class="u-name login-common" placeholder="请输入手机号码" v-validate="'required|myPhoneNumeric|phone'" :class="{'input': true, 'danger': errors.has('phone') }" name="phone" v-model="phone" id="phone-input"/>
                    <span v-show="errors.has('phone')" class="text-danger">{{ errors.first('phone') }}</span>
                    <i class="user-close-icon" id="user-close"></i>
                </div>
                <div class="user-name-box user-pawd-box2">
                    <div class="u-passwd-iconbox">
                        <i class="pwd-icon-top"></i>
                        <i class="pwd-icon-bottom"></i>
                    </div>
                    <input type="password" class="u-password login-common" placeholder="请输入密码" autocomplete="off" v-validate="'required|pwd'" :class="{'input': true, 'danger': errors.has('pwd') }" name="pwd" v-model="pwd" id="user-pwd"/>
                    <span v-show="errors.has('pwd')" class="text-danger">{{ errors.first('pwd') }}</span>
                  <i class="user-close-icon" id="pwd-close"></i>
                </div>
                <div class="user-name-box user-login-btn">
                    <a @click="checkLog($event)" class="user-login-btnc">登录</a>
                </div>
                <div class="user-name-box forget-paswd">
                    <a  class="reg-quick-btn" @click="goRegisterPage($event)">快速注册</a>
                    <a  class="forget-paswd-btn" @click="goFindPaswdPage($event)">忘记密码</a>
                </div>
            </section>
        </main>
    </div>
</template>
<script>
    import customValidate from '../../../assets/js/customValidate';
    import Api from '../../../api/api';
    import {dealLogin} from '../../../api/login';
    import {dealWeiXinLogin} from '../../../api/login';
    export default{
        data(){
          return{
              phone:'',
              pwd:'',
              token:'',
              isShowLogin:true
          }
        },
        mounted(){
            if(this.isweixin()){
              this.checkUser();
            }else{
              this.makeFocus();
              this.changeInStyle();
              this.switchClose();
            }
        },
        methods:{
          checkUser(){
            let uri = encodeURIComponent(window.location.href);
            let api = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxb8b90178cdd55ad0&redirect_uri=${uri}&response_type=code&scope=snsapi_userinfo&state=123&#wechat_redirect`;
            if(sessionStorage.getItem("code")!==null && sessionStorage.getItem("state")!==null){
              this.getUserInfo(sessionStorage.getItem("code"));
            }else{
              if(window.location.href.indexOf("code")>0){
                const code = this.getQueryString("code");
                const state = this.getQueryString("state");
                if(code!==null && state!==null){
                  sessionStorage.setItem("code",code);
                  sessionStorage.setItem("state",state);
                  this.getUserInfo(code);
                }
              }else{
                window.location = api;
              }
            }
          },
          /**
           * 获取用户自动登录所需要的信息
           * 包括用户名(不是中文名)和token
           */
          getUserInfo(code){
            const _this = this;
            dealWeiXinLogin("/api-token-auth/",code).then((res)=>{
              _this.initAutoLogin(res);
              _this.$store.dispatch('hideLoading');
            },(err)=>{ //微信登录错误信息处理
              if (parseInt(err.error_code) === 50002) {
                _this.showMsg("登录前请先关注磁易购公众号!");
              }
              _this.$store.dispatch('hideLoading');
            });
          },
          /**
           * 初始化自动登录信息
           * */
          initAutoLogin(res){
            this.isShowLogin = false;
            this.initLogin(res);
          },
          /**
           * 查询当前的时间信息
           * */
          getQueryString(name){
            const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
            let r = window.location.search.substr(1).match(reg);
            if (r !== null) return decodeURIComponent(r[2]); return null;
          },
          /**
           * 判断是否是微信浏览器的问题
           * */
          isweixin() {
            const ua = window.navigator.userAgent.toLowerCase();
            if(ua.match(/MicroMessenger/i) == 'micromessenger'){
              return true;
            } else {
              return false;
            }
          },
          /**
           * 切换显示的样式
           * */
          switchClose(){
              $("#user-close").on('click',function () {
                $("#phone-input").val("");
                $(this).hide();
              });
              $("#phone-input").bind('input propertychange',function () {
                $("#user-close").show();
              });

              $("#pwd-close").on('click',function () {
                $("#user-pwd").val("");
                $(this).hide();
              });
              $("#user-pwd").bind('input propertychange',function () {
                $("#pwd-close").show();
              });
          },
            /**
             * 登录
             * */
            checkLog($event){
              const that = this;
              if(that.phone==='' || that.pwd === ''){
                this.showMsg('手机号，密码不能为空!');
                return;
              }
              this.$validator.validateAll().then((success)=>{
                  if(!success){
                      return;
                  }
                  dealLogin(Api.LOGIN,that.phone,that.pwd).then((res)=>{
                      if(res.status ===200 && res.data!==null){
                          that.initLogin(res);
                      }else{
                        that.$store.dispatch('hideLoading');
                      }
                  },(err)=>{
                    console.log("登录失败返回的信息:",err);
                    if (parseInt(err.errorcode[0]) === 30000) {
                      that.showMsg("电话号码错误!");
                    }
                    if (parseInt(err.errorcode[0]) === 30001) {
                      that.showMsg("密码错误!");
                    }
                    that.$store.dispatch('hideLoading');
                  })
              },(err)=>{
                that.showMsg("请填写完正确的信息!");
                that.$store.dispatch('hideLoading');
              });
              $event.preventDefault();
            },
            /**
             * 初始化登录的信息
             * */
            initLogin(res){
              this.showMsg("恭喜您，登录成功!");
              this.token = res.data.token;
              localStorage.setItem('token',res.data.token);
              localStorage.setItem('userId',res.data.user_id);
              localStorage.setItem('uName',res.data.user_name);
              localStorage.setItem('login',false);
              this.$store.dispatch('showLogin',false);
              this.$store.dispatch('setToken', res.data.token);
              this.$store.dispatch('setUserId', res.data.user_id);
              let redirect = decodeURIComponent(this.$route.query.redirect || '/');
              this.$router.push({
                path: redirect
              });
            },
            dealErr(){
              this.$store.dispatch('hideLoading');
              this.showMsg("登录失败，请重试!");
            },
            /**
             * 显示出给用户的提示信息
             * */
            showMsg(str){
                this.$toast({
                    message: str,
                    duration: 2000,
                });
            },
            /**
             *
             * 让第一个输入框自动获取到焦点
             * */
            makeFocus(){
                let uEle = $(".u-name")[0];
                let LoginBox = $(".login_layer")[0];
                uEle.focus();
                if(uEle.focus){
                    $(uEle).css({
                        'border':'2px solid #20a0ff',
                    });
                }
            },
            /**
             * 输入框获取到焦点的时候改变样式
             * */
            changeInStyle(){
                let inputs = $(".login-common");
                $(inputs).each(function (index,item) {
                    $(item).focus(function () {
                        $(this).css({
                            'border':'2px solid #20a0ff',
                        });
                    });
                    $(item).blur(function () {
                        $(this).css({
                            'border': '1px solid #d6d6d6',
                        });
                    });
                });
            },
            goRegisterPage($event){
                this.$router.push({path: '/register', replace: true});
                $event.preventDefault();

            },
            goFindPaswdPage($event){
                this.$router.push({path: '/forgetpwd', replace: true});
                $event.preventDefault();
            },

        }
    }
</script>
<style scoped>
    @import "../../../assets/css/login.css";
    body{
        background: #ffffff;
    }
    .text-danger{
        color: #a94442;
        width: 100%;
        display: block;
        text-align: left;
    }
    .danger{
        border: 2px solid #a94442;
    }
</style>



